<template>
  <div class="con">
    <publicTop></publicTop>
    <!-- 头部 -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item
        v-for="(item, index) in lunbo"
        :key="index"
        @click="goLink(item.linkurl)"
      >
        <img :src="item.pictureOne" alt="" />
      </van-swipe-item>
    </van-swipe>
    <div class="home-lists" v-if="listTa.length != 0">
      <div
        v-for="(item, index) in listTa"
        :key="index"
        class="home-lists-li"
        @click="goSub(item.title, item.href)"
      >
        <!-- <img src="@/assets/icon-icon.png" alt="" /> -->
        <img :src="item.image" alt="" />
        <div class="home-lists-li-text">{{ item.title }}</div>
      </div>
    </div>

    <!-- 日历 -->
    <Calendar></Calendar>
    <!-- 新闻列表 -->
    <div class="home-tabs">
      <div class="home-tabs-scroll">
        <div
          v-for="(item, index) in newList"
          :key="index"
          class="home-tabs-li"
          :class="irNew == index ? 'home-tabs-li-active' : ''"
          @click="goTabNew(index, item.pName, item.pId)"
        >
          <div class="home-tabs-li-text">{{ item.pName }}</div>
        </div>
      </div>
    </div>
    <div class="list-all5" v-if="subNewsList.length != 0">
      <div
        v-for="(item, index) in subNewsList"
        :key="index"
        class="list-all5-li"
        @click="goNewsDetail(item.id)"
      >
        <div class="list-all5-li-top">
          {{ item.title }}
        </div>
        <div class="list-all5-li-end">
          <div class="list-all5-li-end-l">
            来源于{{ item.publisher }}
            <span>{{ item.hits }}次浏览数</span>
          </div>
          <div class="list-all5-li-end-r">{{ item.releaseDate }}</div>
        </div>
      </div>
    </div>
    <div class="more-text1" v-if="subNewsList.length != 0" @click="goNewsMore">
      <img src="@/assets/btn-more.png" alt="" />
    </div>
    <!-- 底部列表 -->
    <div class="home-tabs">
      <div class="home-tabs-scroll">
        <div
          v-for="(item, index) in tabList"
          :key="index"
          class="home-tabs-li"
          :class="ir == index ? 'home-tabs-li-active' : ''"
          @click="goTab(index)"
        >
          <div class="home-tabs-li-text">{{ item.name }}</div>
        </div>
      </div>
    </div>

    <div class="list-all" v-if="subNewsArray.length != 0">
      <div
        v-for="(item, index) in subNewsArray"
        :key="index"
        class="list-all-li"
        @click="goDetail(item.id)"
      >
        <img
          src="@/assets/status-up.png"
          alt=""
          class="list2-all-li-status"
          v-if="
            jjType == 'recruitmentFair' &&
            item.fairType != 1 &&
            item.fairType != 2
          "
        />
        <img
          src="@/assets/status-down.png"
          alt=""
          class="list2-all-li-status"
          v-if="
            jjType == 'recruitmentFair' &&
            (item.fairType == 1 || item.fairType == 2)
          "
        />
        <div
          class="list-all-li-ab1"
          v-if="jjType == 'recruitmentinfo1' || jjType == 'recruitmentinfo2'"
        >
          {{ item.startTime }}
        </div>
        <div
          class="list-all-li-l"
          v-if="jjType == 'recruitmentinfo1' || jjType == 'recruitmentinfo2'"
        >
          <img
            :src="item.corporationinfo.logoUrl"
            alt=""
            v-if="item.corporationinfo.logoUrl"
          />
          <img src="@/assets/momo.png" alt="" v-else />
        </div>
        <div
          class="list-all-li-r"
          v-if="jjType == 'recruitmentinfo1' || jjType == 'recruitmentinfo2'"
        >
          <div class="list-all-li-r-t">
            {{ item.title }}
          </div>
          <div class="list-all-li-r-m">
            <div class="list-all-li-r-m-r" :style="`color:${bgc}`">
              {{ item.corporationinfo.name }}
            </div>
          </div>
          <div
            class="list-all-li-r-r"
            v-if="item.positionNum != 0 || item.positionNum != ''"
          >
            {{ item.positionNum }}个职位 | {{ item.education }}
          </div>
          <div v-else class="list-all-li-r-r">见详情页</div>
        </div>

        <img
          src="@/assets/status-up.png"
          alt=""
          class="list2-all-li-status"
          v-if="jjType == 'bilateralchosefair' && item.fairType != 1"
        />
        <img
          src="@/assets/status-down.png"
          alt=""
          class="list2-all-li-status"
          v-if="jjType == 'bilateralchosefair' && item.fairType == 1"
        />
        <div class="list2-all-li-r" v-if="jjType == 'bilateralchosefair'">
          <div class="list2-all-li-r-t">
            <div class="list2-all-li-r-t-text">
              {{ item.title }}
            </div>
          </div>
          <div class="list2-all-li-r-m">
            <img src="@/assets/gray.png" alt="" class="list2-all-li-r-m-l" />
            <div class="list2-all-li-r-m-r">{{ item.place }}</div>
          </div>
          <div class="list2-all-li-r-r">
            <img src="@/assets/timehei.png" alt="" />
            <div class="list2-all-li-r-r-text">{{ item.startTime }}</div>
          </div>
        </div>

        <div class="list3-all-li-l" v-if="jjType == 'recruitmentFair'">
          <img
            :src="item.corporationinfo.logoUrl"
            alt=""
            v-if="item.corporationinfo.logoUrl"
          />
          <img src="@/assets/momo.png" alt="" v-else />
        </div>
        <div class="list3-all-li-r" v-if="jjType == 'recruitmentFair'">
          <div class="list3-all-li-r-t">
            {{ item.title }}
          </div>
          <div class="list3-all-li-r-m">
            <img src="@/assets/gray.png" alt="" class="list3-all-li-r-m-l" />
            <div class="list3-all-li-r-m-r">{{ item.place }}</div>
          </div>
          <div class="list3-all-li-r-r">
            <img src="@/assets/timehei.png" alt="" />
            <div class="list3-all-li-r-r-text">{{ item.startTime }}</div>
          </div>
        </div>
      </div>
    </div>

    <div class="more-text" v-if="subNewsArray.length != 0">
      <img src="@/assets/btn-more.png" alt="" @click="goTo" />
    </div>

    <HomeTop :i="checkIndex" />
  </div>
</template>

<script>
import qs from "qs";
import date from "@/utils/date.js";
import HomeTop from "@/components/homeTop.vue";
import Calendar from "@/components/calendar.vue";
import publicTop from "@/components/publicTop.vue";
export default {
  name: "newIndex",
  components: {
    HomeTop,
    Calendar,
    publicTop,
  },
  data() {
    return {
      checkIndex: 0,
      tabList: [],
      inputContent: "",
      bgc: this.GLOBAL.bgc,
      subNewsArray: [],
      jjType: "",
      paramY: "",
      ir: 0,
      scroll: 0,
      listTa: [],
      lunbo: [],
      newList: [],
      irNew: 0,
      subNewsList: [],
      pName: "",
      pId: "",
      pId36: "",
    };
  },
  beforeRouteLeave(to, from, next) {
    if (
      to.path == "/recruitmentinfoForm" ||
      to.path == "/recruitmentFairForm" ||
      to.path == "/bilateralchosefairForm" ||
      to.path == "/Calendar" ||
      to.path == "/newsDetail"
    ) {
      this.$store.commit("GET_CATCHE_COMPONENTS", ["newIndex"]);
    } else {
      this.$store.commit("GET_CATCHE_COMPONENTS", []);
    }
    next();
  },
  methods: {
    goNewsMore() {
      // sessionStorage.setItem("name", this.pName);
      // this.$router.push({ name: "newsList" });

      // sessionStorage.setItem("pIdNews", this.pId);

      // 此处url加id是为了和pc能呼应，不影响逻辑
      // 这种写法是为了对应pc，且刷新正常，url传参获取，而不是缓存id
      this.$router.push({
        path: "/newsList",
        query: { id: this.pId },
      });
    },
    goTabNew(index, name, id) {
      console.log(name, id);
      // 此处url加id是为了和pc能呼应，不影响逻辑
      this.pId = id;
      this.pName = name;
      this.irNew = index;
      this.subNewsList = this.newList[index].articleList;
    },
    goNewsDetail(id) {
      // this.$store.commit("updataNewsId", id);
      // this.$router.push({ name: "newsDetail" });
      this.$router.push({
        path: "/newsDetail",
        query: { id: id },
      });
    },
    goLink(url) {
      console.log(url);
      if (url) {
        window.location.href = url;
      }
    },
    goTab(index) {
      this.ir = index;
      for (var j = 0; j < this.tabList.length; j++) {
        if (index == j) {
          this.jjType = this.tabList[j].type;
          // this.subNewsArray = this.tabList[j].list;

          if (this.tabList[j].list.length >= 5) {
            this.subNewsArray = this.tabList[j].list.slice(0, 5);
          } else {
            this.subNewsArray = this.tabList[j].list;
          }
        }
      }
    },
    goTo() {
      if (
        this.jjType == "recruitmentinfo1" ||
        this.jjType == "recruitmentinfo2"
      ) {
        // this.$router.push({ name: "zhaoMore" });
        if (this.jjType == "recruitmentinfo1") {
          this.paramY = 0;
          this.$router.push({
            path: "/recruitmentinfoList",
            query: { type: 1 },
          });
        } else {
          this.paramY = 1;
          this.$router.push({
            path: "/recruitmentinfoList",
            query: { type: 2 },
          });
        }
        this.$store.commit("updataTypeText", this.paramY);
      } else if (this.jjType == "recruitmentFair") {
        // this.$router.push({ name: "xuanMore" });
        this.$router.push({ name: "recruitmentFairList" });
      } else {
        // this.$router.push({ name: "shuangMore" });
        // this.$router.push({ name: "bilateralchosefairList" });
        this.$router.push({
          path: "/bilateralchosefairList",
          query: { type: 4 },
        });
      }
    },
    goDetail(id) {
      console.log(id, this.jjType);
      // this.$store.commit("updataDetailId", id);
      if (
        this.jjType == "recruitmentinfo1" ||
        this.jjType == "recruitmentinfo2"
      ) {
        // this.$router.push({ name: "zhaoDetail" });
        this.$router.push({
          path: "/recruitmentinfoForm",
          query: { type: "", positionDetailId: id },
        });
      } else if (this.jjType == "recruitmentFair") {
        // this.$router.push({ name: "xuanDetail" });
        this.$router.push({
          path: "/recruitmentFairForm",
          query: { type: 3, id: id },
        });
      } else {
        // this.$router.push({ name: "shuangDetail" });
        this.$router.push({
          path: "/bilateralchosefairForm",
          query: { type: 4, id: id },
        });
      }
    },
    goSub(title, href) {
      console.log(title, href);
      // if (title == "双选会") {
      //   this.$router.push({ name: "shuangMore" });
      // } else if (title == "招聘信息") {
      //   this.$router.push({ name: "zhaoMore" });
      //   this.$store.commit("updataTypeText", 0);
      // } else if (title == "宣讲会") {
      //   this.$router.push({ name: "xuanMore" });
      // } else {
      // }

      window.location.href = href;

      // this.$router.push({ name: "activityMore" });

      // 此处url加id是为了和pc能呼应，不影响逻辑
      // console.log(this.pId36, "474585");
      // this.$router.push({ path: "/newsList", query: { id: this.pId36 } });
    },
    getLists() {
      const params = {
        type: "mobile",
        num: 9,
      };
      console.log(params);
      this.$axios
        // .get(
        //   "/f/ajaxHome/ajax_recruitmentActivityList?type=" + "mobile" + "&num=9"
        // )
        .post("/f/ajaxHome/ajax_recruitmentActivityList", qs.stringify(params))
        .then((res) => {
          console.log(res, "数据list");
          if (res.data.state == 1) {
            if (res.data.data && res.data.data.length != 0) {
              this.jjType = res.data.data[0].type;
              for (var j = 0; j < res.data.data.length; j++) {
                if (
                  res.data.data[j].list &&
                  res.data.data[j].list.length != 0
                ) {
                  for (var w = 0; w < res.data.data[j].list.length; w++) {
                    res.data.data[j].list[w].startTime = res.data.data[j].list[
                      w
                    ].startTime.slice(0, 10);
                  }
                }
              }
              this.tabList = res.data.data;

              if (res.data.data[0].list.length >= 5) {
                this.subNewsArray = res.data.data[0].list.slice(0, 5);
              } else {
                this.subNewsArray = res.data.data[0].list;
              }

              // this.subNewsArray = res.data.data[0].list;
            }
          } else {
            this.tabList = [];
            this.subNewsArray = [];
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
    getListTT() {
      const params1 = {
        type: "mobile",
        num: 6,
      };
      console.log(params1);
      this.$axios
        // .get("/f/ajaxHome/ajax_quickEntryList?type=mobile" + "&num=" + 6)
        .post("/f/ajaxHome/ajax_quickEntryList", qs.stringify(params1))
        .then((res) => {
          console.log(res, "中部");
          if (res.data.state == 1) {
            if (res.data.data) {
              this.listTa = res.data.data;
            }
          } else {
            this.listTa = [];
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
    getBanner() {
      this.$axios
        .post("/f/ajaxHome/ajax_IndexSwiper")
        .then((res) => {
          console.log(res, "轮播");
          if (res.data.state == 1) {
            if (res.data.object) {
              this.lunbo = res.data.object;
            }
          } else {
            this.lunbo = [];
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
    getNewsList() {
      const params2 = {
        type: "mobile",
        num: 6,
      };
      console.log(params2);
      this.$axios
        // .get("/f/ajaxHome/ajax_indexNewsList?type=mobile" + "&num=" + 6)
        .post("/f/ajaxHome/ajax_indexNewsList", qs.stringify(params2))
        .then((res) => {
          console.log(res, "新闻");
          if (res.data.state == 1) {
            if (res.data.data && res.data.data.length != 0) {
              this.newList = res.data.data;
              if (
                res.data.data[0].articleList &&
                res.data.data[0].articleList.length != 0
              ) {
                for (var i = 0; i < res.data.data.length; i++) {
                  for (
                    var j = 0;
                    j < res.data.data[i].articleList.length;
                    j++
                  ) {
                    res.data.data[i].articleList[j].releaseDate = date
                      .formatTimeTwo(
                        res.data.data[i].articleList[j].releaseDate / 1000,
                        "Y-M-D"
                      )
                      .slice(0, 10);
                  }
                }
                this.subNewsList = res.data.data[0].articleList;
                this.pId = res.data.data[0].pId;
              }
            }
          } else {
            this.newList = [];
            this.subNewsList = [];
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },

    kh() {
      let that = this;
      const params3 = {
        type: "mobile",
      };
      console.log(params3);
      that.$axios
        .post("/f/ajaxHome/ajax_categoryMapList", qs.stringify(params3))
        .then((res) => {
          console.log(res, "新闻");
          if (res.data.state == 1) {
            if (res.data.data && res.data.data.length != 0) {
              if (res.data.data[0].module == "article") {
                that.pId36 = res.data.data[0].id;
              }
            }
          } else {
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
  },
  created() {
    console.log(this.$store.getters.getCOMPONENTS, "11");
  },
  mounted() {
    this.getLists();
    this.getListTT();
    this.getBanner();
    this.getNewsList();
    this.kh();
  },
  beforeDestory() {},
  destroyed() {},
};
</script>
<style lang="css" scoped>
