  background-color: #f5f5f5;
  padding-bottom: 1.66rem;
  min-height: 100%;
}
.home-lists {
  width: 7.5rem;
  background-color: #ffffff;
  padding: 0.52rem 0.44rem 0rem 0.44rem;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.home-lists-li {
  margin-bottom: 0.44rem;
  margin-right: 0.8rem;
}
.home-lists-li:nth-child(4n) {
  margin-right: 0rem !important;
}
.home-lists-li img {
  width: 0.88rem;
  height: 0.88rem;
}
.home-lists-li-text {
  font-family: PingFangSC-Regular;
  font-size: 0.26rem;
  font-weight: normal;
  font-stretch: normal;
  line-height: 0.36rem;
  letter-spacing: 0rem;
  color: #3f4967;
}

