  letter-spacing: 0rem;
  color: #b3b3b3;
}

.my-swipe {
  width: 7.5rem;
  height: 3.67rem;
}
.my-swipe .van-swipe-item {
  width: 7.5rem;
  height: 3.67rem;
}
.my-swipe .van-swipe-item img {
  width: 7.5rem;
  height: 3.67rem;
}
.home-tabs-li-active::after {
  position: absolute;
  width: 100%;
