}
/* 双选会列表 */
.list2-all {
  width: 7.5rem;
  padding: 0 0.32rem;
  box-sizing: border-box;
}
.list2-all-li {
  width: 6.86rem;
  background-color: #ffffff;
  border-radius: 0.12rem;
