  font-stretch: normal;
  letter-spacing: 0rem;
  color: #c1c4ce;
  right: 0.32rem;
  bottom: 0.36rem;
}
.list2-all-li-l {
  width: 1.2rem;
  height: 1.22rem;
  margin-right: 0.24rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.list2-all-li-l img {
  width: 1.2rem;
  height: 1.22rem;
}
.list2-all-li-r {
  width: 6.22rem;
}

.list2-all-li-r-t {
  width: 6.22rem;
  display: flex;
  align-items: center;
}
.list2-all-li-r-t-text {
  width: 6rem;
  font-family: PingFangSC-Medium;
  font-size: 0.32rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #2b2e34;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  word-break: break-all;
  text-align: left;
}
.list2-all-li-r-m {
  display: flex;
  align-items: center;
  margin-top: 0.1rem;
  margin-bottom: 0.1rem;
}
.list2-all-li-r-m-l {
  width: 0.3rem;
  height: 0.34rem;
  margin-right: 0.1rem;
}
.list2-all-li-r-m-r {
  font-family: PingFangSC-Medium;
  font-size: 0.24rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #999ca8;
  width: 5.7rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
